import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Sights from '../views/Sights.vue'
import Maps from '../views/Map.vue'
import Kurashiki from '../views/Kurashiki.vue'
import OkayamaCastle from '../views/OkayamaCastle.vue'
import JeansStreet from '../views/JeansStreet.vue'


Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About,
  },
  {
    path: '/sights',
    name: 'Sights',
    component: Sights,
    children:[
      {path:'Kurashiki', component: Kurashiki},
      {path:'OkayamaCastle', component: OkayamaCastle},
      {path:'JeansStreet', component: JeansStreet},
    ]
  },
  {
    path: '/map',
    name: 'Map',
    component: Maps,
  },
  {
    path: '*',
    redirect: '/',
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
